const PuddingMenu = () => {
  
  let pudding = useSelector(state => {return state.pudding})

  useEffect(() => {
    SwiperCore.use([Navigation]);
  }, []);
  return(
    <div className='swiper-btn-position'>
      <Swiper 
      modules={[Navigation]}
      slidesPerView={4} 
      spaceBetween={40} 
      id='menu-slider' 
      navigation={{
        nextEl: '.custom-swiper-button-next',
        prevEl: '.custom-swiper-button-prev',
      }}
      onSlideChange={() => console.log('slide change')}
      onSwiper={(swiper) => console.log(swiper)}
      >
        {
          pudding.map((a, i) => {
            return (
              <SwiperSlide key={i} className='menu-slide'>
                <Link className='menu-link' to={'/menu/' + a.id}>
                  <img src={'img/pudding/item0' + i + '.png'} alt={pudding[i].name}/>
                  <p className='menu-contents menu-name'>{pudding[i].name}</p>
                  <p className='menu-contents menu-price'>{pudding[i].price}</p>
                </Link>
              </SwiperSlide>
            )
          })
        }
      </Swiper>
      <button className='custom-swiper-button-prev'></button>
      <button className='custom-swiper-button-next'></button>
    </div>
  )
}
const SeasonMenu = () => {

  let seasonPudding = useSelector(state => {return state.SeasonMenu})

  return(
    <Swiper slidesPerView={4} id='season-menu-slider'>
      {
        seasonPudding.map((a, i) => {
          return (
            <SwiperSlide key={i} className='season-slide'>
              <Link className='menu-link' to={'/menu/' + a.id}>
                <img src={'img/pudding/item0' + seasonPudding[i].id + '.png'}/>
                <p className='menu-contents menu-name'>{seasonPudding[i].name}</p>
                <p className='menu-contents menu-price'>{seasonPudding[i].price}</p>
              </Link>
            </SwiperSlide>
          )
        })
      }
    </Swiper>
  )
}
