import { useDispatch } from "react-redux"
import { useSelector } from "react-redux/es/hooks/useSelector"
import { useParams } from "react-router-dom"
import { plusCart, minusCart } from "../store"


const Cart = () => {

    let allItems = useSelector(state => {return state.allItems})
    let cart = useSelector(state => {return state.cart})
    let {id} = useParams()
    let showItem = allItems.find(x => {return x.id == id})
    let dispatch = useDispatch()
    return(
        <table className="cart">
            <thead>
                <th>#</th>
                <th>상품명</th>
                <th>가격</th>
                <th>수량</th>
                <th>추가</th>
                <th>제거</th>
            </thead>
            <tbody>
                {
                    cart.map((a, i) => {
                        return(
                            <tr key={i}>
                                <td>{cart[i].id}</td>
                                <td>{cart[i].name}</td>
                                <td>{cart[i].priceNum * cart[i].count}원</td>
                                <td>{cart[i].count}</td>
                                <td><button className="cart-plus-btn" onClick={()=>{ dispatch(plusCart(cart[i])) }}>+</button></td>
                                <td><button className="cart-minus-btn" onClick={()=>{ dispatch(minusCart(cart[i])) }}>-</button></td>
                            </tr>
                        )
                    })
                }
            </tbody>
        </table>
    )
}

export default Cart