import { useEffect } from "react";
const { kakao } = window

const Locate = () => {
    useEffect(()=>{
        var container = document.getElementById('map'); //지도를 담을 영역의 DOM 레퍼런스
        var options = { //지도를 생성할 때 필요한 기본 옵션
            center: new kakao.maps.LatLng(33.405921, 126.256476), //지도의 중심좌표.
            level: 3 //지도의 레벨(확대, 축소 정도)
        };

        var map = new kakao.maps.Map(container, options); //지도 생성 및 객체 리턴
        //마커가 표시 될 위치
        let markerPosition = new kakao.maps.LatLng(
            33.405921,
            126.256476
        );
    
        // 마커를 생성
        let marker = new kakao.maps.Marker({
            position: markerPosition,
        });
    
        // 마커를 지도 위에 표시
        marker.setMap(map);
    }, [])
    
    return(
        <section className="sc1320 locate">
            <h4 id="menu-tt">LOCATE</h4>
            <div className="locate-container">
                <div id="map" style={{width: '45%', height: '500px'}}></div>
                <div className="locate-contents">
                    <ul>
                        <li>ADDRESS : <span>제주시 한림읍 한림로542-1</span></li>
                        <li>TELEPHONE : <span>0507-1327-0064</span></li>
                        <li>OPEN : <span>09:00</span></li>
                        <li>CLOSE : <span>20:00</span></li>
                    </ul>
                </div>

            </div>
        </section>
    )
}

export default Locate