export let { addItem, minusItem } = allItems.actions
let cart = createSlice({
    name: 'cart',
    initialState: [],
    reducers: {
        plusCart: (state, action) => {
            let thisItem = state.findIndex(e => { return e.id === action.payload.id})
            if(thisItem != -1){
                state[thisItem].count++
            }else{
                state.push(action.payload)
            }
        },
        minusCart: (state, action) => {
            let thisItem = state.findIndex(e => {return e.id === action.payload.id})
            if(state[thisItem].count > 1){
                state[thisItem].count--
            }else{
                state.splice(thisItem, 1)
            }
        }
    }
})
export let { plusCart, minusCart } = cart.actions
