import { useState } from "react"
import { useSelector } from "react-redux"
import { Link } from 'react-router-dom'


const Menu = () => {
  let menu = useSelector(state => {return state.menu})
  let [menuTab, setMenuTab] = useState(0)
  console.log(menu)
  return(
    <section className="sc1320 sc-menu">
      <h4 id="menu-tt">MENU</h4>
      <div className="menu-tab">
        {
          menu.map((a, i) => {
            return(
              <div className={`menu-tab-list ${menuTab === i ? 'active' : ''}`} onClick={()=>{ setMenuTab(i) }} key={i}>{menu[i]}</div>
            )
          })
        }
      </div>
      <MenuTab menuTab={menuTab}/>
      {/* <MenuPudding/>
      <MenuSnack/>
      <MenuFabric/>
      <MenuLiving/>
      <MenuAccessary/> */}
    </section>
  )
}
const MenuAll = () => {
  let allItems = useSelector(state => {return state.allItems})
  return(
    <div className="menu-tab-container">
      {
        allItems.map((a, i) => {
          return(
            <Link className={`menu-tab-link${i === 4 ? ' no-margin-right' : ''}`} to={'/menu/' + a.id} key={i}>
              <img src={allItems[i].img}/>
              <p className='menu-tab-contents pudding-name'>{allItems[i].name}</p>
              <p className='menu-tab-contents pudding-price'>{allItems[i].price}</p>
            </Link>
          )
        })
      }
    </div>
  )
}
